'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useAuth } from '@/contexts/AuthContext';
import { 
  ChevronDownIcon, 
  MagnifyingGlassIcon, 
  FunnelIcon, 
  CheckCircleIcon, 
  ClockIcon,
  XCircleIcon
} from '@heroicons/react/24/outline';

enum RequestStatus {
  PENDING = 'pending',
  ACCEPTED = 'accepted',
  IN_PROGRESS = 'in_progress',
  DELIVERED = 'delivered',
  REVISION = 'revision',
  COMPLETED = 'completed',
  CANCELLED = 'cancelled',
  REJECTED = 'rejected'
}

interface ServiceRequest {
  id: string;
  title: string;
  description: string;
  category: string;
  price: number;
  status: RequestStatus;
  createdAt: string;
  dueDate?: string;
  client: {
    id: string;
    name: string;
    image?: string;
  };
  service?: {
    id: string;
    title: string;
  };
  attachments?: number;
  messages?: number;
}

export default function ServiceRequestsPage() {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [requests, setRequests] = useState<ServiceRequest[]>([]);
  const [filteredRequests, setFilteredRequests] = useState<ServiceRequest[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<RequestStatus | 'all'>('all');
  const [sortBy, setSortBy] = useState<'newest' | 'oldest' | 'price_high' | 'price_low'>('newest');
  
  useEffect(() => {
    // محاكاة جلب البيانات من API
    const fetchRequests = async () => {
      setLoading(true);
      
      // بيانات تجريبية
      const mockRequests: ServiceRequest[] = [
        {
          id: 'req1',
          title: 'تصميم شعار شركة بالذكاء الاصطناعي',
          description: 'أريد تصميم شعار لشركتي الجديدة في مجال التقنية باستخدام Midjourney على أن يكون الشعار مميز وعصري',
          category: 'توليد صور',
          price: 150,
          status: RequestStatus.PENDING,
          createdAt: '2023-12-01T10:30:00Z',
          client: {
            id: 'client1',
            name: 'محمد العبدالله',
            image: '/images/avatars/client1.jpg'
          },
          service: {
            id: 's1',
            title: 'توليد صور احترافية باستخدام Midjourney و DALL-E'
          },
          attachments: 2,
          messages: 4
        },
        {
          id: 'req2',
          title: 'كتابة محتوى لموقع شركة برمجيات',
          description: 'نحتاج إلى كتابة محتوى لموقع شركتنا المتخصصة في تطوير التطبيقات. يشمل ذلك صفحة رئيسية، من نحن، خدماتنا، ومدونة بعدد 5 مقالات.',
          category: 'كتابة محتوى',
          price: 300,
          status: RequestStatus.ACCEPTED,
          createdAt: '2023-11-28T14:15:00Z',
          dueDate: '2023-12-10T23:59:59Z',
          client: {
            id: 'client2',
            name: 'سارة الأحمد',
            image: '/images/avatars/client2.jpg'
          },
          service: {
            id: 's2',
            title: 'كتابة محتوى إبداعي باستخدام GPT-4'
          },
          attachments: 1,
          messages: 6
        },
        {
          id: 'req3',
          title: 'تصميم قصة متحركة للأطفال',
          description: 'أحتاج إلى تصميم قصة متحركة للأطفال من عمر 5-8 سنوات عن البيئة والحفاظ عليها. يجب أن تكون مناسبة للأطفال وتعليمية.',
          category: 'قصص متحركة',
          price: 450,
          status: RequestStatus.IN_PROGRESS,
          createdAt: '2023-11-20T09:45:00Z',
          dueDate: '2023-12-05T23:59:59Z',
          client: {
            id: 'client3',
            name: 'فيصل المطيري',
            image: '/images/avatars/client3.jpg'
          },
          attachments: 3,
          messages: 8
        },
        {
          id: 'req4',
          title: 'تحليل بيانات المبيعات وإنشاء تقرير شامل',
          description: 'لدينا بيانات مبيعات لمدة 3 سنوات ونحتاج إلى تحليلها وإنشاء تقرير شامل مع رسوم بيانية وتوصيات لتحسين المبيعات',
          category: 'تحليل بيانات',
          price: 600,
          status: RequestStatus.DELIVERED,
          createdAt: '2023-11-15T11:20:00Z',
          dueDate: '2023-11-30T23:59:59Z',
          client: {
            id: 'client4',
            name: 'نورة الغامدي',
            image: '/images/avatars/client4.jpg'
          },
          attachments: 5,
          messages: 12
        },
        {
          id: 'req5',
          title: 'تطوير روبوت محادثة لخدمة العملاء',
          description: 'نحتاج إلى تطوير روبوت محادثة ذكي لموقعنا لمساعدة العملاء في الإجابة على الأسئلة الشائعة وتوجيههم',
          category: 'الذكاء الاصطناعي',
          price: 800,
          status: RequestStatus.REVISION,
          createdAt: '2023-11-10T15:30:00Z',
          dueDate: '2023-11-25T23:59:59Z',
          client: {
            id: 'client5',
            name: 'عبدالله العتيبي',
            image: '/images/avatars/client5.jpg'
          },
          attachments: 4,
          messages: 15
        },
        {
          id: 'req6',
          title: 'توليد أفكار لحملة تسويقية',
          description: 'نحتاج إلى أفكار إبداعية لحملة تسويقية على وسائل التواصل الاجتماعي لإطلاق منتج جديد',
          category: 'كتابة محتوى',
          price: 200,
          status: RequestStatus.COMPLETED,
          createdAt: '2023-11-05T13:20:00Z',
          client: {
            id: 'client6',
            name: 'ليلى الحربي',
            image: '/images/avatars/client6.jpg'
          },
          attachments: 0,
          messages: 5
        },
        {
          id: 'req7',
          title: 'تصميم صور لوسائل التواصل الاجتماعي',
          description: 'أحتاج إلى تصميم 10 صور لحسابات التواصل الاجتماعي الخاصة بمتجري الإلكتروني للملابس',
          category: 'توليد صور',
          price: 250,
          status: RequestStatus.CANCELLED,
          createdAt: '2023-10-28T09:15:00Z',
          client: {
            id: 'client7',
            name: 'طارق الشمري',
            image: '/images/avatars/client7.jpg'
          },
          attachments: 1,
          messages: 3
        }
      ];
      
      setTimeout(() => {
        setRequests(mockRequests);
        setFilteredRequests(mockRequests);
        setLoading(false);
      }, 1000);
    };
    
    fetchRequests();
  }, []);
  
  useEffect(() => {
    // تصفية وترتيب الطلبات
    let result = [...requests];
    
    // تصفية حسب البحث
    if (searchTerm) {
      result = result.filter(
        request =>
          request.title.includes(searchTerm) ||
          request.description.includes(searchTerm) ||
          request.client.name.includes(searchTerm) ||
          request.category.includes(searchTerm)
      );
    }
    
    // تصفية حسب الحالة
    if (statusFilter !== 'all') {
      result = result.filter(request => request.status === statusFilter);
    }
    
    // ترتيب النتائج
    switch (sortBy) {
      case 'newest':
        result.sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
        break;
      case 'oldest':
        result.sort((a, b) => new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime());
        break;
      case 'price_high':
        result.sort((a, b) => b.price - a.price);
        break;
      case 'price_low':
        result.sort((a, b) => a.price - b.price);
        break;
    }
    
    setFilteredRequests(result);
  }, [requests, searchTerm, statusFilter, sortBy]);
  
  const handleStatusChange = (requestId: string, newStatus: RequestStatus) => {
    // محاكاة تحديث حالة الطلب
    setRequests(prevRequests =>
      prevRequests.map(request =>
        request.id === requestId
          ? { ...request, status: newStatus }
          : request
      )
    );
  };
  
  const getStatusBadge = (status: RequestStatus) => {
    switch (status) {
      case RequestStatus.PENDING:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
            قيد الانتظار
          </span>
        );
      case RequestStatus.ACCEPTED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
            تم القبول
          </span>
        );
      case RequestStatus.IN_PROGRESS:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-indigo-100 text-indigo-800">
            قيد التنفيذ
          </span>
        );
      case RequestStatus.DELIVERED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-purple-100 text-purple-800">
            تم التسليم
          </span>
        );
      case RequestStatus.REVISION:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-orange-100 text-orange-800">
            قيد المراجعة
          </span>
        );
      case RequestStatus.COMPLETED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
            مكتمل
          </span>
        );
      case RequestStatus.CANCELLED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800">
            ملغي
          </span>
        );
      case RequestStatus.REJECTED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
            مرفوض
          </span>
        );
    }
  };
  
  // تحويل التاريخ إلى صيغة مقروءة
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('ar-SA', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };
  
  return (
    <div className="bg-white shadow rounded-lg">
      <div className="px-4 py-5 sm:px-6 border-b border-gray-200">
        <h3 className="text-lg font-medium leading-6 text-gray-900">إدارة الطلبات الواردة</h3>
        <p className="mt-1 text-sm text-gray-500">
          اطلع على طلبات الخدمات الواردة من العملاء وقم بإدارتها
        </p>
      </div>
      
      {/* أدوات البحث والتصفية */}
      <div className="p-4 border-b border-gray-200 bg-gray-50">
        <div className="flex flex-col sm:flex-row justify-between space-y-3 sm:space-y-0 sm:space-x-4 sm:space-x-reverse">
          <div className="relative flex-1">
            <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
              <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
            </div>
            <input
              type="text"
              className="block w-full pr-10 p-2 border border-gray-300 rounded-md focus:ring-indigo-500 focus:border-indigo-500 text-right"
              placeholder="ابحث عن طلب..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          
          <div className="flex space-x-3 space-x-reverse">
            <div className="relative inline-block text-right">
              <select
                className="block appearance-none bg-white border border-gray-300 rounded-md py-2 pr-3 pl-10 text-base focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                value={statusFilter}
                onChange={(e) => setStatusFilter(e.target.value as RequestStatus | 'all')}
              >
                <option value="all">جميع الحالات</option>
                <option value={RequestStatus.PENDING}>قيد الانتظار</option>
                <option value={RequestStatus.ACCEPTED}>تم القبول</option>
                <option value={RequestStatus.IN_PROGRESS}>قيد التنفيذ</option>
                <option value={RequestStatus.DELIVERED}>تم التسليم</option>
                <option value={RequestStatus.REVISION}>قيد المراجعة</option>
                <option value={RequestStatus.COMPLETED}>مكتمل</option>
                <option value={RequestStatus.CANCELLED}>ملغي</option>
                <option value={RequestStatus.REJECTED}>مرفوض</option>
              </select>
              <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">
                <FunnelIcon className="h-4 w-4 text-gray-400" />
              </div>
            </div>
            
            <div className="relative inline-block text-right">
              <select
                className="block appearance-none bg-white border border-gray-300 rounded-md py-2 pr-3 pl-10 text-base focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value as any)}
              >
                <option value="newest">الأحدث أولاً</option>
                <option value="oldest">الأقدم أولاً</option>
                <option value="price_high">السعر: من الأعلى</option>
                <option value="price_low">السعر: من الأقل</option>
              </select>
              <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3">
                <ChevronDownIcon className="h-4 w-4 text-gray-400" />
              </div>
            </div>
          </div>
        </div>
      </div>
      
      {/* قائمة الطلبات */}
      <div className="overflow-x-auto">
        {loading ? (
          <div className="text-center py-12">
            <div className="inline-block w-12 h-12 rounded-full border-4 border-indigo-300 border-t-indigo-600 animate-spin"></div>
            <p className="mt-3 text-gray-500">جاري تحميل الطلبات...</p>
          </div>
        ) : filteredRequests.length === 0 ? (
          <div className="text-center py-12">
            <p className="text-gray-500">لا توجد طلبات تطابق معايير البحث</p>
          </div>
        ) : (
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  الطلب
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  العميل
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  التصنيف
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  السعر
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  التاريخ
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  الحالة
                </th>
                <th scope="col" className="px-6 py-3 text-center text-xs font-medium text-gray-500 uppercase tracking-wider">
                  الإجراءات
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredRequests.map((request) => (
                <tr key={request.id}>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="ml-4">
                        <div className="text-sm font-medium text-gray-900">
                          <Link href={`/dashboard/requests/${request.id}`} className="hover:text-indigo-600">
                            {request.title}
                          </Link>
                        </div>
                        <div className="text-sm text-gray-500 line-clamp-1 max-w-xs">
                          {request.description}
                        </div>
                        {request.attachments && request.attachments > 0 && (
                          <div className="mt-1 text-xs text-gray-500">
                            <span className="mr-2">المرفقات: {request.attachments}</span>
                            {request.messages && request.messages > 0 && (
                              <span>الرسائل: {request.messages}</span>
                            )}
                          </div>
                        )}
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="flex-shrink-0 h-8 w-8 relative">
                        <Image
                          className="rounded-full object-cover"
                          src={request.client.image || "/images/avatar.jpg"}
                          alt={request.client.name}
                          fill
                          onError={(e) => {
                            const target = e.target as HTMLImageElement;
                            target.src = "/images/avatar.jpg";
                          }}
                        />
                      </div>
                      <div className="mr-3">
                        <div className="text-sm font-medium text-gray-900">
                          <Link href={`/providers/${request.client.id}`} className="hover:text-indigo-600">
                            {request.client.name}
                          </Link>
                        </div>
                      </div>
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-900">{request.category}</div>
                    {request.service && (
                      <div className="text-xs text-gray-500">{request.service.title}</div>
                    )}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-gray-900">{request.price} ر.س</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    <div className="flex flex-col">
                      <span>تاريخ الطلب: {formatDate(request.createdAt)}</span>
                      {request.dueDate && (
                        <span className="text-xs mt-1">
                          تاريخ التسليم: {formatDate(request.dueDate)}
                        </span>
                      )}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    {getStatusBadge(request.status)}
                  </td>
                  <td className="px-4 py-4 whitespace-nowrap text-center text-sm font-medium">
                    <div className="flex justify-center space-x-2 space-x-reverse">
                      <Link
                        href={`/dashboard/requests/${request.id}`}
                        className="inline-flex items-center px-2.5 py-1.5 border border-gray-300 text-xs font-medium rounded text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                      >
                        عرض التفاصيل
                      </Link>
                      
                      {request.status === RequestStatus.PENDING && (
                        <>
                          <button
                            onClick={() => handleStatusChange(request.id, RequestStatus.ACCEPTED)}
                            className="inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded text-white bg-green-600 hover:bg-green-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500"
                          >
                            <CheckCircleIcon className="ml-1 h-4 w-4" />
                            قبول
                          </button>
                          
                          <button
                            onClick={() => handleStatusChange(request.id, RequestStatus.REJECTED)}
                            className="inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500"
                          >
                            <XCircleIcon className="ml-1 h-4 w-4" />
                            رفض
                          </button>
                        </>
                      )}
                      
                      {request.status === RequestStatus.ACCEPTED && (
                        <button
                          onClick={() => handleStatusChange(request.id, RequestStatus.IN_PROGRESS)}
                          className="inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                        >
                          <ClockIcon className="ml-1 h-4 w-4" />
                          بدء العمل
                        </button>
                      )}
                      
                      {request.status === RequestStatus.IN_PROGRESS && (
                        <button
                          onClick={() => handleStatusChange(request.id, RequestStatus.DELIVERED)}
                          className="inline-flex items-center px-2.5 py-1.5 border border-transparent text-xs font-medium rounded text-white bg-purple-600 hover:bg-purple-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-purple-500"
                        >
                          تسليم
                        </button>
                      )}
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
} 